@import url(https://fonts.googleapis.com/css?family=Josefin+Sans|Raleway:300|Muli|Nothing+You+Could+Do&display=swap);
html {
  height: 100%;
  background: linear-gradient(135deg, #0b1324 0%, #1d272c 100%);
}

body {
  height: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
}
.App {
  text-align: center; }
  .App .landing {
    margin-top: 200px; }
  .App .logoContainer {
    position: fixed;
    right: 20px;
    top: 20px;
    cursor: pointer; }
    .App .logoContainer a {
      text-decoration: none;
      color: white;
      font-family: 'Nothing you could do', sans-serif; }

.searchForm {
  padding: 40px 30px 0 30px;
  display: flex;
  justify-content: center;
  background-color: inherit;
  -webkit-transition: margin-top 0.1s;
  transition: margin-top 0.1s; }
  .searchForm .searchInput {
    font-family: 'Raleway', sans-serif;
    line-height: 1.2;
    background-color: inherit;
    color: white;
    outline: none;
    border: none;
    border-bottom: solid 1px #6528ff;
    font-size: 1.5em;
    padding: 5px 8px; }
  .searchForm .searchButton {
    background-color: inherit;
    border: 0;
    outline: 0; }
    .searchForm .searchButton .searchIcon {
      width: 25px;
      height: 25px; }

.searchResults {
  padding: 20px 50px;
  text-align: left;
  line-height: 2; }
  .searchResults .resultTitle {
    font-size: 2em;
    background: -webkit-gradient(linear, left top, right top, from(#13f0fc), color-stop(40%, #6528ff));
    background: linear-gradient(to right, #13f0fc 0%, #6528ff 40%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }
  .searchResults .titleUnderline {
    background: -webkit-gradient(linear, left top, right top, from(#13f0fc), to(#6528ff));
    background: linear-gradient(to right, #13f0fc 0%, #6528ff 100%);
    width: 200px;
    height: 5px;
    margin: 5px 0 20px 0; }
  .searchResults .inlineContainer {
    padding: 10px 0; }
    .searchResults .inlineContainer .inlineHeading {
      padding: 5px 10px;
      margin: 0 20px 0 0;
      background: -webkit-gradient(linear, left top, right top, from(#5816f6), color-stop(50%, #6528ff));
      background: linear-gradient(to right, #5816f6 0%, #6528ff 50%); }
    .searchResults .inlineContainer .inlineItem {
      padding: 5px 7px;
      cursor: pointer;
      margin: 0 10px;
      border-radius: 2px;
      background: -webkit-gradient(linear, left top, right top, from(#212121), to(#2a2a2a));
      background: linear-gradient(to right, #212121 0%, #2a2a2a 100%); }
  .searchResults .definitionHeading {
    border-bottom: solid 2px pink;
    padding-bottom: 5px; }
  .searchResults .wordType {
    font-size: 0.8em;
    background-color: #1b1b1b;
    font-style: italic;
    padding: 2px 5px;
    color: orange;
    margin-right: 15px; }
  .searchResults .wordDefinition {
    font-weight: 300; }

.errorMessage {
  background: -webkit-gradient(linear, left top, right top, from(#13f0fc), to(#6528ff));
  background: linear-gradient(to right, #13f0fc 0%, #6528ff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 2em;
  margin-top: 50px; }

@media (max-width: 425px) {
  .searchForm {
    position: absolute;
    z-index: 3;
    bottom: 30px;
    padding: 0;
    width: 100%; }
  .searchResults {
    height: calc(100vh - 150px);
    overflow-y: scroll; }
  .inlineContainer {
    overflow-x: scroll;
    white-space: nowrap; }
    .inlineContainer .inlineHeading {
      margin-bottom: 30px !important; } }

